y.")]),_c('p',{staticClass:"coach-para body-1"},[_vm._v("Kathi holds a MA in Organizational Development from The California Institute of Integral Studies and a BA in Public Relations from the University of Michigan. She completed graduate-level work in Cross-Cultural Studies at the Universite de Lyon II while working in France for several years.")]),_c('p',{staticClass:"coach-para body-1"},[_vm._v("Kathi has the pleasure of living in the San Francisco area with her two blooming daughters and a 50 pound lap dog named Coco")]),_c('p',{staticClass:"coach-para body-1"},[_c('span',{staticStyle:{"color":"#6E2000"}},[_vm._v("MASTER STATUS:")]),_vm._v(" Executives in Transition, Executive Presence, Multi-Intelligence Approach to Development and Problem Solving, Subtle Body Assessment and Practice Plans\n        ")])])])]),_c('div',{staticClass:"coach",staticStyle:{"margin-top":"30px"}},[_c('div',{staticClass:"coach-desc"},[_c('p',{staticClass:"display-1 coach-name"},[_vm._v("Saara Robles")]),_c('p',{staticClass:"title"},[_vm._v("Executive Coach and Leadership Consultant")]),_c('p',{staticClass:"font-weight-medium font-italic"},[_vm._v("Specializing in Executive and Leadership Team Development and Equine-Guided Leadership")]),_c('p',{staticClass:"title"},[_vm._v("\"True leadership is knowing when to step forward and when to step back; when to lean in and when to give space for others to take center stage. It's about knowing how to leverage the very best from your team.\"")]),_c('p',{staticClass:"coach-para body-1"},[_vm._v("Saara believes that you cannot serve the world or others at your highest level, until you know who you are and how you impact the world.Kathi is in the service of bringing out the inspired brilliance in leaders, teams, and organizations that wish to fulfill their wildest dreams.")]),_c('p',{staticClass:"coach-para body-1"},[_vm._v("Saaras executive leadership and teamwork spans over 25 years. Her direct, pragmatic approach creates the best environment for others to rise to their best levels of leadership, whether thats for work, community, family or in service to making the world a better place. Saara has worked in every type of business and industry with leaders across the globe partnering to enable their greatest contributions to the organizations and teams they lead. Her view is that knowing oneself first is an underpinning that creates leaders who are their most satisfied, productive, real selves.")]),_c('p',{staticClass:"coach-para body-1"},[_vm._v("She is committed to people finding joy in their work, no matter the level, environment or role. Her mantra remains if people are to have passion for what they do, then going to work each day shouldnt be a chore, it should be an opportunity.")]),_c('p',{staticClass:"coach-para body-1"},[_c('span',{staticStyle:{"color":"#6E2000"}},[_vm._v("MASTER STATUS:")]),_vm._v("\n          Leading Large Scale Change, Transforming Culture, Inclusionary Engagement, Executive Presence, Guiding Teams to Their Top Performance, Developing Leaders Who Others Love to Follow\n        ")])]),_c('img',{staticClass:"bio-img",attrs:{"src":"https://res.cloudinary.com/ducvha2fk/image/upload/v1559943938/lpage-bio/saara-opa.webp"}})])])}]

