<template>
  <div>
    <!-- Dashboard Header -->
    <v-toolbar dark color="secondary" app>

      <v-toolbar-side-icon class="hidden-xs-only" @click="sideNavBar = !sideNavBar"></v-toolbar-side-icon>
      <img style="width:192px" src="https://res.cloudinary.com/ducvha2fk/image/upload/v1559515269/oplogo.png">

      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-menu v-if="userHasGroups === true">
          <template #activator="{ on: menu }">
            <v-btn flat v-on="{ ...menu }">Your Groups</v-btn>
          </template>
          <v-list>
            <div v-for="(group, index) in groups" :key="index">
              <v-list-tile @click="goToGroup(group.title, group.id)">
                <v-list-tile-title>{{ group.title }}</v-list-tile-title>
              </v-list-tile>
            </div>
          </v-list>
        </v-menu>
        <v-menu>
          <template #activator="{ on: menu }">
            <v-btn flat v-on="{ ...menu }">Settings</v-btn>
          </template>
          <v-list>
            <div>
              <v-list-tile @click="goToUploadImage">
                <v-list-tile-title>Change Profile Picture</v-list-tile-title>
              </v-list-tile>
            </div>
          </v-list>
        </v-menu>
        <v-btn flat @click="signOut">Sign out</v-btn>
      </v-toolbar-items>
      <!-- <v-toolbar-side-icon class="hidden-sm-and-up" @click="mobilePanel = !mobilePanel"></v-toolbar-side-icon> -->
    </v-toolbar>

    <!-- End Header -->

    <!-- Side Nav Bar  -->

    <v-navigation-drawer width="230" v-model="sideNavBar" app class="grey darken-3" disable-resize-watcher temporary>
      <v-expansion-panel v-model="panel" expand v-if="personalAccess===true">

        <v-expansion-panel-content v-for="item in personalChapters" :key="item.id">
          <template v-slot:header>
            <div>{{ item.title }}</div>
          </template>
          <div v-for="course in item.personal_courses" :key="course.id">
            <v-card @click="goToCourse(course.id)">
              <v-card-text class="grey lighten-3">
                {{ course.title }}
                <div style="display:inline-block" v-if="course.users[0]">
                  <div v-for="(complete,index) in course.users" :key="index">
                    <v-icon v-if="complete.id == userParam" color="success" right>done</v-icon>
                  </div>
                </div>
              </v-card-text>
            </v-card>
          </div>
        </v-expansion-panel-content>
      </v-expansion-panel>
      <!-- Group Side Nav Bar -->
      <v-expansion-panel v-else v-model="panel" expand >
          <v-card flat min-width=100% min-height="65px">
              <v-card-text class="display-1">
                  {{ groupName }}
              </v-card-text>
          </v-card>
        <v-expansion-panel-content v-for="item in gChapters" :key="item.id">
          <template v-slot:header>
            <div>{{ item.title }}</div>
          </template>
          <div v-for="course in item.group_courses" :key="course.id">
            <v-card @click="goToGroupCourse(course.id)">
              <v-card-text class="grey lighten-3">
                {{ course.title }}
                <div style="display:inline-block">
                  <div v-for="(complete,index) in course.group_course_completes" :key="index">
                    <v-icon v-if="complete.userId == userParam && complete.groupId == groupId && complete.groupCourseId == course.id" color="success" right>done</v-icon>
                  </div>
                </div>
              </v-card-text>
            </v-card>
          </div>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-navigation-drawer>
    <!-- End Side Nav Bar -->

    <!-- Mobile Expansion Panel -->
    <!-- <v-navigation-drawer
      width="220"
      class="hidden-sm-and-up accent"
      right
      v-model="mobilePanel"
      app
    >
      <v-expansion-panel v-model="panel" expand>
        <v-expansion-panel-content>
          <template #activator="{ on: menu }">
            <v-btn flat v-on="{ ...menu }">Your Groups</v-btn>
          </template>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-navigation-drawer>-->
    <!-- End Mobile Expansion Panel -->

    <!-- Dashboard Content -->

    <!-- Profile Pic -->
    <v-container class="my-5">
       <v-layout row wrap justify-center align-center>
        <v-flex xs12 sm12 md3 lg3 xl3>
        <v-sheet elevation="8" class="mx-auto" min-height="200" width= "250px" style="border-radius:10px">
          <div style="padding:5px">
            <radial-progress-bar
              :diameter="240"
              :completed-steps="totalComplete"
              :total-steps="totalSteps"
              :strokeWidth="15"
              startColor="green"
              stopColor="white"
              :animateSpeed="900"
            >
              <div class="main-icon-div">
                <img class="profile-pic" :src="image">
              </div>
            </radial-progress-bar>
          </div>
         </v-sheet>
        </v-flex>

         <v-flex  xs12 sm12 md12 lg9 xl9>
           <v-sheet elevation="8" class="mx-auto" min-height="250"  style="border-radius:10px;">
          <div style="padding:5px; margin:15px">
          <h1 style="margin-bottom:-10px; font-weight:300; text-align:center; padding-top:10px; font-size:42px"> Steve Albonico</h1> 
          <h2 style="font-weight:300; text-align:left; padding-top:10px"> <b>Company:</b> Albonico LLC</h2> 
          <h2 style="font-weight:300; text-align:left; padding-top:10px"> <b>Position:</b> Owner and CEO</h2> 
          <h2 style="font-weight:300; text-align:left; padding-top:10px"> <b>Favorite Color:</b> Purple</h2> 
          <h1 style="font-weight:400; text-align:center; padding-top:10px"> Total Progress: {{this.overallProgress}}</h1>  
          </div>
           </v-sheet>
        </v-flex>


      </v-layout>
    </v-container>
   
   <!-- Courses  -->
   
    <v-sheet elevation="8" class="mx-auto" min-height="250" width="90%" style="border-radius:10px; margin-top:-25px; margin-bottom:20px">
    <h1 style="font-weight:300; text-align:center; padding-top:10px"> Your Personal Pillars Progress</h1> 
    
    <v-container>
      <v-layout row wrap justify-space-between v-if="personalAccess === true">
        <v-flex v-for="(chapter,index) in personalChapters" :key="index" xs6 sm3 md3 lg1 xl1>
          <h2 style="font-weight:300; text-align:center; width: 150px"> {{chapter.title}}</h2> 
          <div @click="goToAnswerPage(chapter.id)">

            <radial-progress-bar
              :diameter="160"
              :completed-steps="value[index]"
              :total-steps="totalSteps"
              :strokeWidth="15"
              startColor="green"
              stopColor="white"
              :animateSpeed="900"
            >
              <img class="icon" :src="icons[index]" alt>
            </radial-progress-bar>
            <h2 style="font-weight:300; text-align:center; width: 150px">{{progress[index]}}</h2> 
          </div>
        </v-flex>
      </v-layout>
    </v-container>
    </v-sheet>
    <!-- End DashBoard Content  -->
  </div>
</template>
<script>
import Service from "../Service";
import RadialProgressBar from "vue-radial-progress";
import { parse } from "path";

export default {
  components: {
    RadialProgressBar
  },
  name: "Dashboard",
  data() {
    return {
        personalAccess: true,
      userParam: this.$route.params.id,
      sideNavBar: false,
      mobilePanel: false,
      panel: [false],
      user: {},
      personalChapters: [],
      userHasGroups:false,
      groups: [],
      image: "",
      groupName: '',
      gChapters:[],
      groupId:null,
      // Progress bar state
      progress: [],
      overallProgress: "",
      value: [],
      completedSteps: 0,
      totalSteps: 100,
      totalComplete: 0,
      // End Progress bar state
      icons: [
        "data:image/png;base64,",
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSt0NcsDQYLxFiDkt4BV8-3xclXxkYhvfACKmIKNz-seSSPnXLcXA",
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRA9DJGEs0drLffSMJDxGwFMRo-mIzxAnWWTuwZr8Q0sqI8gAZV",
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQdQxUAnbcWVFMKevtUKxfLAtIHIATG6iLRpPYTyeIQuzbxxuSg",
        "https://upload.wikimedia.org/wikipedia/en/thumb/e/e0/WPVG_icon_2016.svg/1024px-WPVG_icon_2016.svg.png",
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTzOT4uekAcWnhWFJ691CSzeyaw81YVHWYXTes30KLNGqqeGag_Xw",
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR3Qx5GWpzEnfTx7YQWX4rEjENki6qHoic0uyknmY5W20ph81cYJw"
      ],
    
    };
  },
    watch: {
    async $route(to, from) {
        this.$router.push('/login');
    }
  },
  async created() {
    try {
      // Get User Data
      var userData = await Service.getUserProfile(this.$route.params.id);
      if (userData.data.error) {
        this.$router.push(`/login`);
      } else {
        this.user = userData.data.user;
        this.image = userData.data.user.image;
        

        if(this.user.personal_access === false) {
            this.personalAccess = false;
            var groupChapters = await Service.getOneGroupChapters(this.$route.params);
            this.groupName = groupChapters.data.user.groups[0].title;
            this.groupId = groupChapters.data.user.groups[0].id;
            this.gChapters = groupChapters.data.chapter;
        }

        // Get all groups that this user belongs to
        var groupData = await Service.getGroups(this.$route.params.id);
        if(groupData.data.usersGroups) {
            if(groupData.data.usersGroups.groups.length > 0) {
                this.groups = groupData.data.usersGroups.groups;
                this.userHasGroups = true;
            }
        } 

        // Get all personal chapters and courses
        var personalChaptersData = await Service.getPersonalChapters();
        this.personalChapters = personalChaptersData.data.data;
        
        // Get all personal course completes
        var userTotal = 0;
        var courseTotal = 0;

        for (var i in this.personalChapters) {
          var getCompletes = await Service.getCompletes(
            this.$route.params.id,
            this.personalChapters[i].id
          );

          // Concatenating Users overall progress and putting in data()
          userTotal += getCompletes.data.user.count;
          var localCourseTotal = getCompletes.data.courses.count;

            // Concatenating Users individual course progress and putting in data()
            var completes = `${getCompletes.data.user.count}/${
              getCompletes.data.courses.count
            }`;
            this.progress.push(completes);

          // Getting value for progress bars
            var numerator = parseInt(completes);
            this.value.push(numerator * (100/localCourseTotal));
        
            courseTotal +=localCourseTotal
            this.overallProgress = `${userTotal}/${courseTotal}`;
            
        }
        var split = this.overallProgress.split("/");

        var result = parseInt(split[0], 10) / parseInt(split[1], 10);

        this.totalComplete = Math.floor(result * 100);

       
      }
    } catch (err) {
      console.log(err);
    }
  },
  methods: {
    goToGroup(groupTitle, groupId) {
      this.$router.push(
        `/group-dashboard/${this.$route.params.id}/${groupId}/${groupTitle}`
      );
    },
    goToCourse(courseId) {
      this.$router.push(`/dashboard/${this.$route.params.id}/${courseId}`);
    },
    goToUploadImage() {
      this.$router.push(`/dashboard/${this.$route.params.id}/upload`);
    },
    goToAnswerPage(courseId) {
      this.$router.push(
        `/dashboard/${this.$route.params.id}/summary/${courseId}`
      );
    },
    goToGroupCourse(courseId) {
        this.$router.push(
            `/dashboard/${this.$route.params.id}/${this.groupName}/${this.groupId}/${courseId}`      
        );
    },
    signOut() {
      Service.signOutUser();
      this.$router.push(`/login`);
    }
  }
};
</script>
<style scoped>
.main-icon-div {
  border: 5px solid rgba(97, 24, 24, 0.986);
  border-radius: 100px;
  height: 200px;
  width: 200px;
  display: block;
}
.profile-pic {
  width: 100%;
  height: 100%;
  border-radius: 51%;
}
.main-icon {
  width: 100%;
}
.course-icon {
  border: 1px solid black;
  border-radius: 75px;
  height: 100px;
  width: 100px;
  display: inline-block;
  vertical-align: top;
}
.icon-wrapper {
  display: inline-block;
  vertical-align: top;
  margin: auto;
}
.icon {
  width: 92%;
  height: 78%;
  border-radius: 51%;
}
.v-progress-circular {
  border-radius: 51%;
}

</style>


